.tool {
    visibility: hidden;
    display: flex;
    width: 100%;
    height: 100%;
    background: rgba(0,0,0,0.5);
    position: fixed;
    top: 0;
    left: 0;
    justify-content: center;
    align-items: center;
    transition: visibility .2s, opacity 0.5s linear;
    opacity: 0;
}